import React from "react";
import './credit.scss';
import CheckCircleOutlineIcon from '@material-ui/icons/CheckCircleOutline';

import api from '../../../api';
import { Link } from 'react-router-dom';
import Load from '../../../componets/Loading/Load';



class CreditApproved extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            credits_approved: this.props ? this.props.credits : [],
            loading: true,
        }
        this.abortController = new AbortController();
        this.signal = this.abortController.signal;
    }

    componentDidMount() {
        this.loading()
    }
    loading = () => {
        this.setState({
            ...this.state,
            loading: false,
        });
    }

    componentWillUnmount() {
        this.abortController.abort();
    }
    render() {
        if (this.state.loading) {
            return <Load />
        }
        let credits_approved2 = this.props.credits ? this.props.credits : [];
        let request_A = '';
        if (this.props.credits) {
            request_A = Object.values(this.props.credits).map((credit) => {
                return (
                    <React.Fragment>
                            <div className='col-2 detail-icon'>
                                <CheckCircleOutlineIcon fontSize="large" />
                            </div>
                            <div className='col-10 detail-content'>
                                <div className='message'>Credito Aprobado</div>
                                <div className='detail-amount'>$ {credit.value} {credit.currency}</div>
                                <div><Link to={{ pathname: '/details_info_credit', state: { id: credit.id } }}>Ver Detalles</Link></div>
                            </div><br />
                    </React.Fragment>

                )

            })
        }
        return (
            <React.Fragment>
                {credits_approved2.length != 0 &&
                    <div className='credit-details approved'>
                       <div className='row m-0'>
                            {request_A}
                        </div>
                    </div>
                }
            </React.Fragment>


        );
    }
}

export default CreditApproved;