import React, { useState } from 'react';
import '../../../../src/assets/css/style.scss';
import Label from './../../../componets/Label/Label';
import Input from './../../../componets/Input/Input';
import RequestMessage from '../../../componets/Messages/RequestMessage';
import { Link } from 'react-router-dom';
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import api from '../../../api';
import Load from '../../../componets/Loading/Load';
import swal from 'sweetalert';

const PasswordUpdate = () => {
    const [password, setPassword] = useState('');
    const [passwordconfirm, setPasswordconfirm] = useState('');
    const [passwordError, setPasswordError] = useState(false);
    const [passwordErrorConfirm, setPasswordErrorConfirm] = useState(false);

    const [disabledButton, setDisabledButton] = useState(true);
    const [messageRequest, setMessageRequest] = useState('');
    const [isloading, setLoading] = useState(false);


    const handleChange = (name, value) => {
        setMessageRequest('');
        switch (name) {
            case 'password':
                if (value.length < 8) {
                    setPasswordError(true);
                    setPasswordErrorConfirm(true);
                    setDisabledButton(true);
                } else {
                    setPasswordError(false);
                    setPassword(value);
                    setDisabledButton(true);
                    setPasswordErrorConfirm(true);
                }
                break;
            default:
                if (password != value) {
                    setPasswordErrorConfirm(true);
                    setDisabledButton(true);
                } else if (!value.trim()) {
                    setPasswordErrorConfirm(true);
                    setDisabledButton(true);
                } else {
                    setDisabledButton(false);
                    setPasswordErrorConfirm(false);
                    setPasswordconfirm(value);
                }
                break;
        }
    };

    const handleSubmit = () => {
        setLoading(true);
        setDisabledButton(true);
        let account = { password: password, password_confirmation: passwordconfirm }
        api.update.changePassword(account)
            .then(res => {
                if (res.errors) {
                    let errorsMe = [];
                    let myError = res.errors;
                    Object.values(myError).forEach((error, i) => {
                        errorsMe.push({
                            label: error[0],
                            id: i,
                        });
                    });
                    console.log(errorsMe)
                    setMessageRequest(errorsMe);

                }
                swal({
                    title: "Correcto!",
                    text: res.message,
                    icon: "success",
                    timer: 2000,
                    button: false
                });
                window.location = '/profile';
            })
            .catch((error) => {
                console.log(error)
                let errorsMe = [];
                let myError = error.response.data.errors;
                Object.values(myError).forEach((error, i) => {
                    errorsMe.push({
                        label: error[0],
                        id: i,
                    });
                });
                console.log(errorsMe)
                setMessageRequest(errorsMe);
                setLoading(false);

            })
    };


    if (isloading == true) { return <Load /> }
    return (

        <div className='card-panel'>
            <Link to="/profile"><KeyboardBackspaceIcon fontSize="large" /> </Link>
            <h3 className='text-center'>Cambiar contraseña</h3>
            <div className='form-group'>
                {messageRequest &&
                    <RequestMessage attribute={messageRequest} />
                }
            </div>

            <div className='form-group'>
                <Label text='Nueva contraseña' />
                <Input
                    attribute={{
                        id: 'password',
                        name: 'password',
                        type: 'password',
                        placeholder: 'Contraseña'
                    }}
                    handleChange={handleChange}
                    param={passwordError}
                />
                {passwordError &&
                    <label className='label-error'>La contraseña debe contener al menos 8 carácteres.</label>
                }

            </div>

            <div className='form-group'>
                <Label text='Confirmar contraseña' />
                <Input
                    attribute={{
                        id: 'passwordconfirm',
                        name: 'passwordconfirm',
                        type: 'password',
                        placeholder: 'Confirmar contraseña',
                    }}
                    handleChange={handleChange}
                    param={passwordErrorConfirm}
                />
                {passwordErrorConfirm &&
                    <label className='label-error'>La confirmación de la contraseña no coincide.</label>
                }
            </div>

            <div className='text-center'>
                <button disabled={disabledButton} className='btn-dark full-width bt-submit' onClick={handleSubmit}>Actualizar Contraseña</button>
            </div>
        </div>
    )
};

export default PasswordUpdate;