import React, { useState } from 'react';
import '../../../src/assets/css/style.scss';
import Label from '../../componets/Label/Label';
import Input from '../../componets/Input/Input';
import Header from '../../componets/Header/Hedear';
import { NavLink } from 'react-router-dom';
import axios from 'axios';
import RequestMessage from '../../componets/Messages/RequestMessage';
import api from '../../api';



const RecoverPassword = () => {
    const [email, setEmail] = useState('');
    const [emailError, setEmailError] = useState(false);

    const [disabledButton, setDisabledButton] = useState(true);
    const [ErrorsMessage, setErrorsMessage] = useState('');
    const [messageRequest, setMessageRequest] = useState('');


    function handleChange(name, value) {
        setMessageRequest('');
        switch (name) {
            case 'email':
                const regex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
                if (!value.trim()) {
                    setEmailError('Correo Electrónico requerido')

                } else if (!regex.test(String(value).toLowerCase())) {
                    setEmailError('Correo Electrónico no válido')
                } else {
                    setEmail(value);
                    setEmailError(false)
                    setDisabledButton(false)
                }
                break;
        }
    };

    function handleSubmit() {
        let account = { email }
        console.log(account)
        api.resetPassword(account)
        .then(response => {
            if ((response.code == 500 && response.type == 'error') || (response.code == 403 && response.type == 'error')) {
                setMessageRequest([{ label: response.data.message, id: 1 }]);
              
            }else{
                window.location = '/login';
                // localStorage.setItem("token", data.access_token);
                //     localStorage.setItem("user", JSON.stringify(data.user));
                //     setIsLogin(true);
                //     window.location = '/credit';
            }
        })
        .catch((error) => {
            console.log(error)
        })
    };
    return (
        <section>
            <Header />
            <div className='card card-form space-form'>
                <h3 className='text-center'>Recuperar Contraseña</h3>
                <div className='form-group'>
                    {messageRequest &&
                        <RequestMessage attribute={messageRequest} />
                    }
                </div>

                <div className='form-group'>
                    <Label text='Correo electrónico' />
                    <Input
                        attribute={{
                            id: 'email',
                            name: 'email',
                            type: 'email',
                            placeholder: 'Correo Electrónico',

                        }}
                        handleChange={handleChange}
                    />
                    {emailError &&
                        <label className='label-error'>{emailError}</label>
                    }
                </div>

                <div className='text-center'>
                    <button disabled={disabledButton} className='btn-dark full-width bt-submit' onClick={handleSubmit}>Recuperar Contraseña</button>
                </div>

                <div className='login-btn-sidebar mt-2'>
                    <p>
                        <NavLink className='link' to="/login">Iniciar Sesión</NavLink>
                    </p>
                </div>
            </div>
        </section>

    )
};

export default RecoverPassword;