import React, { useState } from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import '../../../assets/css/style.scss';
import CloseIcon from '@material-ui/icons/Close';
import moment from 'moment';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        modal: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
        },
        paper: {
            backgroundColor: theme.palette.background.paper,
            border: 'none !important',
            borderRadius: '15px',
            width: '95%',
            maxWidth: '800px',
            maxHeight: '600px',
            overflowY: 'scroll',
            scrollBehavior: 'smooth',
            boxShadow: theme.shadows[5],
            padding: theme.spacing(2, 4, 3),
        },
    }),
);

export default function DetailAbonos(props) {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);

    const handleOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };
    let schedule_pay = '';
    let schedule_payAll = props.details.payments ? props.details.payments : [];

    schedule_pay = Object.values(schedule_payAll).map((schedule) => {
        return (
            <tr key={schedule.id} className='text-center'>
                <td>{schedule.id}</td>
                <td>$ {schedule.quantity} </td>
                <td>{moment(schedule.date).format("DD-MM-YYYY")}</td>
                <td>{schedule.hour}</td>
            </tr>
        )
    })
    return (
        <div>
            <div onClick={handleOpen}>
                <div className='simple-link'>
                    <p>Detalles</p>
                </div>
            </div>
            <Modal
                aria-labelledby="transition-modal-title"
                aria-describedby="transition-modal-description"
                className={classes.modal}
                open={open}
                onClose={handleClose}
                closeAfterTransition
                BackdropComponent={Backdrop}
                BackdropProps={{
                    timeout: 500,
                }}
            >
                <Fade in={open}>
                    <div className={classes.paper}>
                        <div className='data-modal'>
                            <div style={{ textAlign: "right" }}><button onClick={handleClose}><CloseIcon fontSize="large" /></button></div>
                            <div className='modal-header'>
                                <h3>Detalles del Pago Abonado</h3>
                            </div>
                            <div className='modal-body row'>
                                <div className='table-responsive'>
                                    <table className="table">
                                        <thead>
                                            <tr className='text-center'>
                                                <th scope="col">ID</th>
                                                <th scope="col">Cantidad</th>
                                                <th scope="col">Fecha</th>
                                                <th scope="col">Hora</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {props.details.payments.length == 0
                                                ?
                                                <tr className='text-center'>
                                                    <td colSpan="4">No hay Abonos</td>
                                                </tr>
                                                :
                                                schedule_pay
                                            }
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </Fade>
            </Modal>
        </div>
    );
}