import React, { useEffect, useState } from "react";
import api from '../../../api';
import Load from '../../../componets/Loading/Load';
import { Link } from 'react-router-dom';
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import moment from 'moment';
import DetailAbonos from './DetailAbonos';



import { useLocation } from "react-router-dom";

const HistoryPagosClient = () => {
    let credit_date = useLocation();
    useEffect(() => {
        getData();
    }, []);

    const [loading, setLoading] = useState(true);
    const [historialPayments, setHistorialPayments] = useState([]);



    const getData = async () => {
        let credit_user_id = credit_date.state.credit_user_id
        api.lists.getHistoricalPaymentsCredit(credit_user_id)
            .then(res => {
                setLoading(false);
                setHistorialPayments(res);
            });
    };

    if (historialPayments.schedule) {
        let paymentDone = '';
        let paymentDoneAll = historialPayments.schedule ? historialPayments.schedule.payment_done : [];

        paymentDone = Object.values(paymentDoneAll).map((done) => {
            return (
                <tr key={done.schedule_id} className='text-center'>
                    <td>$ {done.quantity}</td>
                    <td>$ {done.total_interest} </td>
                    <td>$ {done.total}</td>
                    <td>{moment(done.due_date).format("DD-MM-YYYY")}</td>
                    <td>{done.payment_state.name}</td>
                </tr>
            )
        })

        let paymentEarly = '';
        let paymentEarlyAll = historialPayments.schedule ? historialPayments.schedule.payed_early : [];

        paymentEarly = Object.values(paymentEarlyAll).map((early) => {
            return (
                <tr key={early.schedule_id} className='text-center'>
                    <td>$ {early.quantity}</td>
                    <td>$ {early.total_interest} </td>
                    <td>$ {early.total}</td>
                    <td>{moment(early.due_date).format("DD-MM-YYYY")}</td>
                    <td>{early.payment_state.name}</td>
                    <td><DetailAbonos details={early} /></td>
                </tr>
            )
        })

        let paymentLate = '';
        let paymentLateAll = historialPayments.schedule ? historialPayments.schedule.late_payments : [];

        paymentLate = Object.values(paymentLateAll).map((late) => {
            return (
                <tr key={late.schedule_id} className='text-center'>
                    <td>$ {late.quantity}</td>
                    <td>$ {late.total_interest} </td>
                    <td>$ {late.total}</td>
                    <td>{moment(late.due_date).format("DD-MM-YYYY")}</td>
                    <td>{late.payment_state.name}</td>
                </tr>
            )
        })

        let paymentNot = '';
        let paymentNotAll = historialPayments.schedule ? historialPayments.schedule.no_payment : [];

        paymentNot = Object.values(paymentNotAll).map((no_payment) => {
            return (
                <tr key={no_payment.schedule_id} className='text-center'>
                    <td>$ {no_payment.quantity}</td>
                    <td>$ {no_payment.total_interest} </td>
                    <td>$ {no_payment.total}</td>
                    <td>{moment(no_payment.due_date).format("DD-MM-YYYY")}</td>
                    <td>{no_payment.payment_state.name}</td>
                </tr>
            )
        })

        let payments_pay = '';
        let payments_payAll = historialPayments.schedule ? historialPayments.schedule.payments : [];

        payments_pay = Object.values(payments_payAll).map((yes_payment) => {
            return (
                <tr key={yes_payment.schedule_id} className='text-center'>
                    <td>$ {yes_payment.quantity}</td>
                    <td>$ {yes_payment.total_interest} </td>
                    <td>$ {yes_payment.total}</td>
                    <td>{moment(yes_payment.due_date).format("DD-MM-YYYY")}</td>
                    <td>{yes_payment.payment_state.name}</td>
                    <td><DetailAbonos details={yes_payment} /></td>
                </tr>
            )
        })

        let paymentReject = '';
        let paymentRejectAll = historialPayments.schedule ? historialPayments.schedule.reject_payment : [];

        paymentReject = Object.values(paymentRejectAll).map((rejected) => {
            return (
                <tr key={rejected.schedule_id} className='text-center'>
                    <td>$ {rejected.quantity}</td>
                    <td>$ {rejected.total_interest} </td>
                    <td>$ {rejected.total}</td>
                    <td>{moment(rejected.due_date).format("DD-MM-YYYY")}</td>
                    <td>{rejected.payment_state.name}</td>
                </tr>
            )
        })

        let paymentTo = '';
        let paymentToAll = historialPayments.schedule ? historialPayments.schedule.to_payment : [];

        paymentTo = Object.values(paymentToAll).map((to_pay) => {
            return (
                <tr key={to_pay.schedule_id} className='text-center'>
                    <td>$ {to_pay.quantity}</td>
                    <td>$ {to_pay.total_interest} </td>
                    <td>$ {to_pay.total}</td>
                    <td>{moment(to_pay.due_date).format("DD-MM-YYYY")}</td>
                    <td>{to_pay.payment_state.name}</td>
                </tr>
            )
        })

        if (loading == true) {
            return <Load />
        } else {
            return (
                <React.Fragment>
                    <section className='container'>
                        <div className='row'>
                            <div className='col-12 mt-4'>
                                <div className='card p-4'>
                                    <div className="col col-12">
                                        <div className='row'>
                                            <div className='col-2'>
                                            <Link style={{textDecoration:'none'}} className='simple-link'  to={{ pathname: '/history_credits_client', state: { id: historialPayments.user_id} }}><KeyboardBackspaceIcon fontSize="large" /> </Link>
                                            </div>
                                            <div className='col-10'>
                                                <h5>Historial de Pagos</h5>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col col-12">
                                        <div className='row'>
                                            <div className='col-4 text-left'>
                                                Folio del Crédito: <b> {historialPayments.folio}</b>
                                            </div>
                                            <div className='col-8 text-right'>
                                                Usuario: <b>{historialPayments.name} {historialPayments.last_name}</b>
                                            </div>
                                        </div>
                                    </div>
                                    <div className='card-body'>
                                        <div className='credit-details'>
                                            <div className='row m-0'>
                                                <div className='col col-10 col-md-11 detail-content'>
                                                    <div className='message text-center'><h3>Pagos Hechos</h3></div>
                                                    <div className='table-responsive'>
                                                        <table className="table">
                                                            <thead>
                                                                <tr className='text-center'>
                                                                    <th scope="col">Cantidad</th>
                                                                    <th scope="col">Intereses Totales</th>
                                                                    <th scope="col">Total Final</th>
                                                                    <th scope="col">Fecha</th>
                                                                    <th scope="col">Estado</th>
                                                                </tr>
                                                            </thead>
                                                            <tbody>
                                                                {historialPayments.schedule.payment_done.length == 0
                                                                    ?
                                                                    <tr className='text-center'>
                                                                        <td colSpan="5">No hay registros</td>
                                                                    </tr>
                                                                    :
                                                                    paymentDone
                                                                }
                                                            </tbody>
                                                        </table>
                                                    </div>
                                                </div>
                                            </div>
                                        </div><br /><hr />
                                        <div className='credit-details'>
                                            <div className='row m-0'>
                                                <div className='col col-10 col-md-11 detail-content'>
                                                    <div className='message text-center'><h3>Pagos Adelantados</h3></div>
                                                    <div className='table-responsive'>
                                                        <table className="table">
                                                            <thead>
                                                                <tr className='text-center'>
                                                                    <th scope="col">Cantidad</th>
                                                                    <th scope="col">Intereses Totales</th>
                                                                    <th scope="col">Total Final</th>
                                                                    <th scope="col">Fecha</th>
                                                                    <th scope="col">Estado</th>
                                                                    <th scope="col">Detalles</th>
                                                                </tr>
                                                            </thead>
                                                            <tbody>
                                                                {historialPayments.schedule.payed_early.length == 0
                                                                    ?
                                                                    <tr className='text-center'>
                                                                        <td colSpan="5">No hay registros</td>
                                                                    </tr>
                                                                    :
                                                                    paymentEarly
                                                                }
                                                            </tbody>
                                                        </table>
                                                    </div>
                                                </div>
                                            </div>
                                        </div><br /><hr />
                                        <div className='credit-details'>
                                            <div className='row m-0'>
                                                <div className='col col-10 col-md-11 detail-content'>
                                                    <div className='message text-center'><h3>Pagos Atrasados</h3></div>
                                                    <div className='table-responsive'>
                                                        <table className="table">
                                                            <thead>
                                                                <tr className='text-center'>
                                                                    <th scope="col">Cantidad</th>
                                                                    <th scope="col">Intereses Totales</th>
                                                                    <th scope="col">Total Final</th>
                                                                    <th scope="col">Fecha</th>
                                                                    <th scope="col">Estado</th>
                                                                </tr>
                                                            </thead>
                                                            <tbody>
                                                                {historialPayments.schedule.late_payments.length == 0
                                                                    ?
                                                                    <tr className='text-center'>
                                                                        <td colSpan="5">No hay registros</td>
                                                                    </tr>
                                                                    :
                                                                    paymentLate
                                                                }
                                                            </tbody>
                                                        </table>
                                                    </div>
                                                </div>
                                            </div>
                                        </div><br /><hr />
                                        <div className='credit-details'>
                                            <div className='row m-0'>
                                                <div className='col col-10 col-md-11 detail-content'>
                                                    <div className='message text-center'><h3>No Pagados</h3></div>
                                                    <div className='table-responsive'>
                                                        <table className="table">
                                                            <thead>
                                                                <tr className='text-center'>
                                                                    <th scope="col">Cantidad</th>
                                                                    <th scope="col">Intereses Totales</th>
                                                                    <th scope="col">Total Final</th>
                                                                    <th scope="col">Fecha</th>
                                                                    <th scope="col">Estado</th>
                                                                </tr>
                                                            </thead>
                                                            <tbody>
                                                                {historialPayments.schedule.no_payment.length == 0
                                                                    ?
                                                                    <tr className='text-center'>
                                                                        <td colSpan="5">No hay registros</td>
                                                                    </tr>
                                                                    :
                                                                    paymentNot
                                                                }
                                                            </tbody>
                                                        </table>
                                                    </div>
                                                </div>
                                            </div>
                                        </div><br /><hr />
                                        <div className='credit-details'>
                                            <div className='row m-0'>
                                                <div className='col col-10 col-md-11 detail-content'>
                                                    <div className='message text-center'><h3>Abonos</h3></div>
                                                    <div className='table-responsive'>
                                                        <table className="table">
                                                            <thead>
                                                                <tr className='text-center'>
                                                                    <th scope="col">Cantidad</th>
                                                                    <th scope="col">Intereses Totales</th>
                                                                    <th scope="col">Total Final</th>
                                                                    <th scope="col">Fecha</th>
                                                                    <th scope="col">Estado</th>
                                                                    <th scope="col">Detalles</th>
                                                                </tr>
                                                            </thead>
                                                            <tbody>
                                                                {historialPayments.schedule.payments.length == 0
                                                                    ?
                                                                    <tr className='text-center'>
                                                                        <td colSpan="5">No hay registros</td>
                                                                    </tr>
                                                                    :
                                                                    payments_pay
                                                                }
                                                            </tbody>
                                                        </table>
                                                    </div>
                                                </div>
                                            </div>
                                        </div><br /><hr />
                                        <div className='credit-details'>
                                            <div className='row m-0'>
                                                <div className='col col-10 col-md-11 detail-content'>
                                                    <div className='message text-center'><h3>Pagos Rechazados</h3></div>
                                                    <div className='table-responsive'>
                                                        <table className="table">
                                                            <thead>
                                                                <tr className='text-center'>
                                                                    <th scope="col">Cantidad</th>
                                                                    <th scope="col">Intereses Totales</th>
                                                                    <th scope="col">Total Final</th>
                                                                    <th scope="col">Fecha</th>
                                                                    <th scope="col">Estado</th>
                                                                </tr>
                                                            </thead>
                                                            <tbody>
                                                                {historialPayments.schedule.reject_payment.length == 0
                                                                    ?
                                                                    <tr className='text-center'>
                                                                        <td colSpan="5">No hay registros</td>
                                                                    </tr>
                                                                    :
                                                                    paymentReject
                                                                }
                                                            </tbody>
                                                        </table>
                                                    </div>
                                                </div>
                                            </div>
                                        </div><br /><hr />
                                        <div className='credit-details'>
                                            <div className='row m-0'>
                                                <div className='col col-10 col-md-11 detail-content'>
                                                    <div className='message text-center'><h3>A Pagar</h3></div>
                                                    <div className='table-responsive'>
                                                        <table className="table">
                                                            <thead>
                                                                <tr className='text-center'>
                                                                    <th scope="col">Cantidad</th>
                                                                    <th scope="col">Intereses Totales</th>
                                                                    <th scope="col">Total Final</th>
                                                                    <th scope="col">Fecha</th>
                                                                    <th scope="col">Estado</th>
                                                                </tr>
                                                            </thead>
                                                            <tbody>
                                                                {historialPayments.schedule.to_payment.length == 0
                                                                    ?
                                                                    <tr className='text-center'>
                                                                        <td colSpan="5">No hay registros</td>
                                                                    </tr>
                                                                    :
                                                                    paymentTo
                                                                }
                                                            </tbody>
                                                        </table>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                </React.Fragment>
            )
        }

    } else {
        if (loading == true) {
            return <Load />
        } else {
            return (
                <React.Fragment>
                    <section className='container'>
                        <div className='row'>
                            <div className='col-12 mt-4'>
                                <div className='card p-4'>
                                    <div className="col col-12">
                                        <div className='row'>
                                            <div className='col-2'>
                                                <Link style={{textDecoration:'none'}} className='simple-link'  to={{ pathname: '/history_credits_client', state: { id: historialPayments.user_id} }}><KeyboardBackspaceIcon fontSize="large" /> </Link>
                                            </div>
                                            <div className='col-10'>
                                                <h5>Historial de Pagos</h5>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col col-12">
                                        <div className='row'>
                                            <div className='col-4 text-left'>
                                                Folio del Crédito:  {historialPayments.folio}
                                            </div>
                                            <div className='col-8 text-right'>
                                                Usuario: <b>{historialPayments.name} {historialPayments.last_name}</b>
                                            </div>
                                        </div>
                                    </div>
                                    <div className='card-body'>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                </React.Fragment>
            )
        }
    }
};

export default HistoryPagosClient;