import React from "react";
import './homeuser.scss';
import PersonIcon from '@material-ui/icons/Person';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import PaymentIcon from '@material-ui/icons/Payment';
import NotificationsIcon from '@material-ui/icons/Notifications';
import { NavLink } from 'react-router-dom';
import api from '../../api.js';

class MenuBottom extends React.Component {
    handleLogout = e => {
        api.logout(localStorage.getItem("token"))
            .then(data => {
                console.log(data);
                localStorage.removeItem("token");
                localStorage.removeItem("user");
                window.location = '/';
            });
    }

    render() {
        return (
            <div className='menu-bottom'>
                <div className='menu-options'>
                    <div className='option'>
                        <NavLink exact activeClassName='active' className='btn-menu' to="/credit"><PaymentIcon /></NavLink>
                    </div>
                    <div className='option'>
                        <NavLink exact activeClassName='active' className='btn-menu' to="/profile"><PersonIcon /></NavLink>
                    </div>
                    <div className='option'>
                        <NavLink exact activeClassName='active' className='btn-menu' to="/notifications"><NotificationsIcon /></NavLink>
                    </div>
                    <div className='option'>
                        <NavLink exact activeClassName='active' className='btn-menu' to='' onClick={this.handleLogout}><ExitToAppIcon /></NavLink>
                    </div>
                </div>
            </div>
        );
    }
}

export default MenuBottom;