import React from "react";
import './footer.scss';
import Logo from '../../assets/images/logo.png';
import EmailIcon from '@material-ui/icons/Email';
import FacebookIcon from '@material-ui/icons/Facebook';
import Terms from '../Modal/Terms';
import Politics from '../Modal/Politics';
import PlayStore from '../../assets/images/play-store.png';
import AppStore from '../../assets/images/app-store.svg';


class Footer extends React.Component {
    render() {
        return (
        <footer>
            <div className='container'>
                <div className='footer-logo'>
                    <img src={Logo} alt="CreditOk Logo" />
                </div>
                <div className='info-contact'>
                    <ul>
                        <li>
                            <p className='text-center'><b>Contáctanos</b></p>
                        </li>
                        <li>
                            <a className='link-footer text-center' href='mailto:contacto@creditok.com'><EmailIcon className='footer-icons mr-2'/> contacto@creditok.com</a>
                        </li>
                    </ul>
                </div>
                <div className='legal-information'>
                    <div className='social'>
                        <p>Síguenos en nuestras redes</p>
                        <a className='social-fb' href='https://www.facebook.com/CreditokMX'><FacebookIcon/></a>
                    </div>
                    <div className='download row'>
                        <p className='col-12'>Descarga nuestra App</p>
                        <div className='col-6 item-app'>
                            <a href='#' >
                                <img src={PlayStore} alt="Descarga la app para Android" />
                            </a>
                        </div>
                        <div className='col-6 item-app'>
                            <a href='#'>
                                <img className='app-store' src={AppStore} alt="Descarga la app para IOS" />
                            </a>
                        </div>
                        
                    </div>
                </div>
            </div>
            <div className='footer-politics'>
                <div className='politics'>
                    <div className='politics-left'>
                        <Terms/>
                        &nbsp; | &nbsp;
                        <Politics/>
                    </div>
                    <div className='politics-right'>
                        México. Yuc. &nbsp; | &nbsp; 2021 &#169; Creditok 
                    </div>
                </div>
            </div>
        </footer>
    );
  }
}

export default Footer;